<template>
	<div class="commodity">
		<mt-header title="三亚专区" class="commodity-header">
			<router-link to="/" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header>
		<div class="tab fl q">
			<h3>排序方式</h3>
			<mt-button type="default" @click="switcherType(1)" :class="{defaultType:this.dataType==1}"><i class="fa fa-thumbs-o-up cahk" aria-hidden="true"></i>长安推荐</mt-button>
			<mt-button type="default" @click="switcherType(2)" :class="{defaultType:this.dataType==2}">星级</mt-button>
			<mt-button type="default" @click="switcherType(3)" :class="{defaultType:this.dataType==3}">价格</mt-button>
		</div>
		<div id="pullrefresh" class="mui-content mui-scroll-wrapper listBody w q">
			<!--数据列表-->
			<ul class="mui-table-view" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="5">
				<li class="mui-table-view-cell mui-media productList "  v-for="(porItem,key) in proList">

					<router-link :to="{ name: 'CommodityDetails',
            			params: { id: porItem.proid  }}">
						<!--<img class="mui-media-object fl" src="../../images/list1.png">-->
						<img class="mui-media-object fl" :src="porItem.src">
						<div class="mui-media-body fr">
							<h5><span>【{{porItem.productname}}】{{porItem.productdesc}}</span></h5>
							<p class='mui-ellipsis'>{{porItem.pricedesc}}</p>
							<div class="flight">
								<p><b class="circle">去</b><span class="flightNumber">{{porItem.goairnum}}</span><span>{{porItem.gotime}}</span></p>
								<p><b class="circle">返</b><span>{{porItem.backairnum}}</span><span>{{porItem.backtime}}</span></p>
							</div>
							<div class="money">￥<span>{{porItem.productprice}}</span>起</div>
						</div>
					</router-link>
				</li>
			</ul>
			<div>
				<div class="pullrefresh-loading" v-if="loading">
					<mt-spinner type="fading-circle" color="#26a2ff"></mt-spinner>
				</div>
				<span v-if="isAllData" class="hintData">{{hintDatBody}}</span>
			</div>

		</div>
	</div>
</template>

<script>
	import api from '../../api/index.js'
	export default {
		name: 'hello',
		data() {
			return {
				site: '三亚',
				dataType: '1',
				proState: 1,
				proid: 123123,
				isAllData: false,
				hintDatBody: '数据加载中...',
				start: 0, //开始记录数
				limit: 5, //本页记录数
				loading: false, //控制加载动画
				proList: []
			}
		},
		mounted() {
			//			this.initData();
		},
		methods: {
			//初始化数据
			initData: function() {
				this.loading = true;
				this.isAllData = true;
				this.hintDatBody="数据加载中...";
				var params = {
					dataType: this.dataType,
					start: this.start,
					proid: this.proid,
					limit: this.limit
				}
				api.getProInfo(params).then(response => {

					//判断数据是否加载完成
					if(this.proList.length > 0) {
						var pageNum = Math.ceil(this.proList[0].totalnum / this.limit);
						if(pageNum == this.start - 1) {
							this.loading = false;
							this.isAllData = true;
							this.hintDatBody = "数据加载完毕"
							return;

						}
					}
					//循环加上图片路径
					for(let i = 0; i < response.proList.length; i++) {
						var imgSrc = {
							proid: response.proList[i].proid,
						}
						//获取图片信息
						api.getProPicture(imgSrc).then(responseImg => {
							if(responseImg.data.length > 0) {
								response.proList[i].src = basePath + responseImg.data[0].img + responseImg.data[0].url;
							}
						})
						this.proList.push(response.proList[i])
					}
					if(this.loading){
						this.hintDatBody="数据加载中...";
					}else{
						this.hintDatBody="上滑加载更多";
					}
					
					this.isAllData = true;	
					this.loading = false;
				}, response => {
					this.hintDatBody = "网络异常";
					this.start = 1;					
					this.loading = false;
					this.isAllData = true;
					return;
				});
				if(this.loading){
						this.hintDatBody="数据加载中...";
					}else{
						this.hintDatBody="上滑加载更多";
					}
				

			},
			//加载假数据
			hotelData: function() {
				for(let i = 0; i < 5; i++) {
					this.proList.push({
						productname: '假数据 ',
						productdesc: '西安—三亚 面朝大海3天2晚自由行',
						pricedesc: '西安出发/三亚京海国际假日酒店',
						airlineInfo: [{
								'linenumber': 'CH7789',
								'startpointnum': '北京',
								'linestart': '出发地',
								'lineDest': '目的地'
							},
							{
								'linenumber': 'CH7789',
								'startpointnum': '北京',
								'linestart': '出发地',
								'lineDest': '目的地'
							},
						],
						hotelpicture: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1502711127069&di=7e21a50ea1d3dc7eb3212a0d993df118&imgtype=0&src=http%3A%2F%2Fpic1.win4000.com%2Fmobile%2F4%2F5367070a961e7_200_300.jpg',
						productprice: 2898
					})
				}
			},
			//触底触发
			loadMore() {
				var _this = this;				
				if(this.hintDatBody=='数据加载完毕' || this.hintDatBody=='网络异常' || this.hinDatBody=="数据加载中...") {
					return;
				}
				if(this.loading) {
					return;
				} else {
					setTimeout(() => {
						this.hintDatBody = "数据加载中..."
						_this.start++;
						_this.initData();
					}, 500);
					this.hintDatBody = "上滑加载更多	"
				}

			},
			//切换类型数据
			switcherType: function(objType) {
				this.hintDatBody = "数据加载中..."
				this.start = 1;
				this.isAllData = false;
				if(this.dataType == objType) {
					return;
				}
				this.proList = [];
				this.dataType = objType;
				this.initData();
			},
		}
	}
</script>

<style lang='scss'>
	.commodity {
		.w {
			width: 6.9rem;
			/*text-align: center;*/
			margin: 0 auto;
		}
		.commodity-header {
			height: .80rem;
			line-height: .80rem;
			background-color: red;
			color: #fff;
			font-size: .35rem;
			.mintui {
				font-size: .35rem;
			}
		}
		.tab {
			/*position: absolute;*/
			z-index: 3;
			width: 100%;
			height: .70rem;
			background-color: rgb(245, 245, 249);
			.defaultType {
				background-color: rgba(255, 204, 29, 0.5);
			}
			.cahk {
				display: inline-block;
				font-size: .2rem;
				width: 20px;
				height: 20px;
				color: rgb(255, 204, 29);
				/*background-color: yellow;*/
			}
		}
		.tab h3,
		.tab button {
			padding: 0;
			font-size: .22rem;
			text-align: center;
			line-height: .40rem;
			width: 22%;
			float: left;
		}
		.tab h3 {
			font-size: .28rem;
			line-height: .70rem;
		}
		.tab button {
			height: .40rem;
			width: 22%;
			border-radius: .22rem;
			margin: .15rem 2%;
			color: rgb(51, 51, 51);
			background-color: rgb(255, 255, 255);
		}
		.listBody {
			margin-top: .8rem;
		}
		.mui-table-view {
			position: static;
		}
		.productList {
			height: 3.60rem;
			/*background-color:yellow ;*/
		}
		.productList a {
			height: 3.60rem;
		}
		.productList .mui-media-object {
			max-width: 9.00rem !important;
			width: 2.40rem;
			height: 3.00rem;
		}
		.productList .mui-media-body {
			height: 3.00rem;
			position: relative;
		}
		.productList h5 {
			white-space: normal;
			word-break: break-all;
			word-wrap: break-word;
			display: block;
			word-wrap: break-word;
			color: rgb(51, 51, 51);
			/*text-indent: 20rem;*/
			line-height: .40rem;
			width: 4.00rem;
			height: 1.00rem;
			font-size: .30rem;
		}
		.productList .mui-ellipsis {
			height: .5rem;
			text-indent: 1em;
			line-height: .40rem;
			color: rgb(153, 153, 153);
			font-size: .22rem;
			white-space: normal;
			word-break: break-all;
			word-wrap: break-word;
		}
		.flight span {
			font-size: .20rem;
			text-align: center;
			line-height: .50rem;
			display: inline-block;
			width: 42%;
			color: rgb(51, 51, 51);
		}
		.flight .circle {
			margin-left: .20rem;
			font-size: .20rem;
			text-align: center;
			line-height: .30rem;
			border-radius: 15rem;
			width: .30rem;
			height: .30rem;
			display: inline-block;
			color: rgb(51, 51, 51);
			background-color: rgb(255, 204, 29);
		}
		.flight .flightNumber {}
		.money {
			color: red;
			font-size: .15rem;
			position: absolute;
			bottom: 0rem;
			right: 0rem;
		}
		.money span {
			color: red;
			font-size: .30rem;
			line-height: .40rem;
		}
		ul.mui-table-view::after {
			display: none;
		}
		/*底部加载动画样式*/
		.pullrefresh-loading {
			.mint-spinner-fading-circle {
				margin: 0 auto;
			}
			/*text-align: center;*/
		}
		.hintData {
			margin-top: .3rem;
			margin-bottom: .1rem;
			/*position: fixed;*/
			/*bottom: 0;*/
			width: .2rem;
			height: .3rem;
			line-height: .3rem;
			width: 100%;
			display: inline-block;
			text-align: center;
		}
	}
</style>


