<template>
	<div class="commodity-details">

		<mt-header class="commodity-back">
			<router-link to="/commodity" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
			<!--<mt-button icon="more" slot="right"></mt-button>-->
		</mt-header>
		<mt-swipe :show-indicators="false">
			<mt-swipe-item v-for="(item,key) in proDetails.secondurl[0]">
				<a href="#"><img :src="basePath2+item" /></a>
			</mt-swipe-item>
			<mt-swipe-item>
				<a href="#"><img :src="basePath2+proDetails.secondurl[0]['1']" /></a>
			</mt-swipe-item>
		</mt-swipe>
		<div class="mui-media-body orderDetails w">
			<!--<h5><span>【长安航假期】&nbsp西安-三亚 &nbsp面朝大海3天2晚自由行</span></h5>-->
			<h5>【{{proDetails.productname}}】&nbsp{{proDetails.productdesc}}</h5>
			<div class="orderDetails-bottom">
				<div class="fl">
					<ul class="star">
						<li class="fl" v-for="item in parseInt(proDetails.hotellevel)"><b class="gain"></b></li>
					</ul>
				</div>
				<div class="money fr"><span>￥{{proDetails.productprice}}</span>起</div>
			</div>

		</div>
		<div class="definite w">
			<span class="fl calendar"></span>
			<!--<mt-button @click.native="handleClick">按钮</mt-button>-->
			<mt-popup v-model="popupVisible" position="auto" modal=false>
				<calendar :datePrice='datePrice' :selectDate="thisDate" v-on:selectDate="selectDate"></calendar>
			</mt-popup>
			<p class="fl " @click="handleClick">
				<span class="hint">不同套餐出发日期及价格</span>
				<span class="scheme">【{{proDetails.productname}}】&nbsp{{proDetails.productdesc}}</span>
			</p>
			<span class="mui-icon mui-icon-arrowright fr" @click="handleClick"><i class="fa fa-angle-right" aria-hidden="true" ></i></span>
		</div>
		<div class="money-specification w">
			<h5>费用说明</h5>
			<h6>费用包含</h6>
			<span v-for="(item,key) in proDetails.feecontain[0]">{{key}}.{{item}}</span>
			<!--<span>1.西安-三亚优质航班往返机票含税</span>
			<span>2.两晚三亚京海国际假日酒店豪华双床&nbsp 不含早</span>
			<span>3.所有费用为单人费用</span>-->
			<h6>费用不含</h6>
			<span v-for="(item,key) in proDetails.feeuncontain[0]">{{key}}.{{item}}</span>
			<!--<span>1.其它任何涉及个人消费</span>-->
		</div>
		<div class="grogshopMessage w">
			<h5>酒店信息</h5>
			<p class="hint">*酒店信息为参考信息，最终酒店信息以实际情况确认为准</p>
			<h6>{{proDetails.hotelList[0].hotelname}}</h6>
			<p>星级：
				<b class="mui-icon mui-icon-star gain" v-for="item in parseInt(proDetails.hotelList[0].hotellevel)"></b>
			</p>
			<span>
				地址：{{proDetails.hotelList[0].hoteladdress}}
				<!--地址：三亚 三亚弯路139号 ， 近 三亚湾椰梦长廊，凤凰机场，海鲜加工市场等。-->
			</span>
			<div class="picture">
			</div>
		</div>
		<div class="productExplain w">
			<h5>产品说明1</h5>
			<h6>出行须知</h6>
			<span>1.西安-三亚优质航班往返机票含税</span>
			<span>2.两晚三亚京海国际假日酒店豪华双床&nbsp 不含早</span>
			<span>3.所有费用为单人费用</span>
			<h6>退改说明</h6>
			<span v-for="(item,key) in proDetails.changerule[0]">{{key}}.{{item}}</span>
		</div>
		<nav class="mui-bar mui-bar-tab commodity-bottom">
			<mt-button class="zx fl">
				<img height="20" width="20" slot="icon"> 咨询
			</mt-button>
			<a slot="left">
				<mt-button class="ljyd fr" @click="promptlySubmit">
					<img height="20" width="20" slot="icon"> 立即预定
				</mt-button>
			</a>
		</nav>
	</div>
</template>

<script>
	import { Popup } from 'mint-ui';
	import calendar from '../../components/calendar/calendar.vue'
	import api from '../../api/index.js'
	export default {
		data() {
			return {
				site: '三亚',
				test:'fretegfdgfdaf',
				scrollTop: 0, //滚动高度
				headerDisplay: true,
				proDetails: {				
					changerule: [{
						'1': "不退",
						'2': "可改签"
					}],
					feecontain: [{
						'1': "西安-三亚优质航班往返机票",
						'2': "两晚豪华房型",
						'3': "其他"
					}],
					feeuncontain: [{
						'1': "个人消费",
						'2': "其他"
					}],
					hotelList: [{
						hotelname: "香格里拉",
						hotellevel: "5",
						hoteladdress: "西安啊啊啊啊",
						hotelpic: "/Freetour/img/111.png"
					}],
					hotellevel: "5",
					productdesc: "古城3天2晚自由行",
					productname: "梦回大唐1",
					productprice: "1500",
					proid: "123123",
					secondurl: [{
						'1': "/FreeTour/img/lasa.png",
						'2': "/FreeTour/img/111.png"
					}]
				},
				hoteMasage: [],
				basePath2: basePath2,
				popupVisible: false,
				thisDate:'',
				datePrice: [], //日期价格展示
			}
		},
		components: {
			'calendar': calendar
		},
		created() {
			this.getInitData(this.$route.params.id);
			window.sessionStorage.proInfo = this.$route.params.id;
		},
		methods: {
			//打开模态框
			handleClick: function() {
				this.$forceUpdate()
//				this.datePrice=this.datePrice;
				console.log(this.datePrice);
				this.popupVisible = true
			},
			getInitData: function(id) {
				api.getOtherInfo({
					proid: id
				}).then(response => {
					this.proDetails = response.data[0];
				});
				api.getHotelInfo({
					proid: id
				}).then(response => {});
				//获取价格表
				var date = new Date();
				var month = date.getMonth() + 1;
				var strDate = date.getDate();
				if(month >= 1 && month <= 9) {
					month = "0" + month;
				}
				if(strDate >= 0 && strDate <= 9) {
					strDate = "0" + strDate;
				}
				var thisDate = date.getFullYear()+""+month + "" + strDate;
				console.log('date.getFullYear()' + date.getFullYear())
				api.getProprice({
					proid: id,
					date: thisDate
				}).then(response => {
					this.datePrice = response.data.slice();
					console.log(this.datePrice);
					//					console.log("价格与日期对应的数据");
				})

			},
			selectDate:function(objDate){
				this.thisDate=objDate;				
				window.sessionStorage.thisDate =this.thisDate;
				this.popupVisible = false
//				console.log(this.thisDate);
			},
			//提交订单
			promptlySubmit:function(){
				if(this.thisDate==''){
					alert("请选择出发时间！");
					return;
				}
				this.$router.push("/OrderMessage");			
			},
			text: function() {
				//console.log(this.$route.params.id)
				var _this = this;
				document.onscroll = function() {
					//console.log(document.body.scrollTop);
					if(document.body.scrollTop > 20) {
						_this.headerDisplay = true;
						_this.changeback(document.body.scrollTop);
					} else {
						_this.headerDisplay = true;
					}
				}
			}
		},

	}
</script>

<style lang='scss'>
	.commodity-details {
		font-size: .14rem;
		.commodity-back {
			background-color: rgba(255, 255, 255, 0);
			position: fixed;
			z-index: 99;
			height: .80rem;
			.mintui-back {
				font-size: .4rem;
				margin-left: .08rem;
			}
		}
		.mint-swipe {
			height: 4.8rem;
		}
		.orderDetails {
			padding-top: .2rem;
			height: 1.9rem;
			position: relative;
			margin-top: .20rem;
		}
		.commodity-header .mui-pull-left {
			color: rgb(255, 255, 255);
		}
		.commodity-header {
			position: fixed;
			z-index: 999;
			width: 100%;
			max-width: 750px;
			background-color: rgba(1, 165, 225, 0.1);
			background-color: none;
			-webkit-box-shadow: none;
			/*background-color: red;*/
		}
		.orderDetails h5 {
			height: 1rem;
			display: inline-block;
			white-space: normal;
			word-break: break-all;
			word-wrap: break-word;
			display: block;
			word-wrap: break-word;
			text-align: left;
			color: rgb(51, 51, 51);
			line-height: .5rem;
			font-size: .32rem;
		}
		.orderDetails .money {
			color: red;
			font-size: .15rem;
			float: right;
			position: absolute;
			bottom: .25rem;
			right: .10rem;
		}
		.orderDetails .money span {
			color: red;
			font-size: .30rem;
			line-height: .40rem;
		}
		.orderDetails-bottom {
			height: .60rem;
			width: 100%;
			line-height: .60rem;
		}
		.orderDetails-bottom .gain {
			background: url("../../images/star-2.png");
			background-size: cover;
			width: .26rem;
			height: .26rem;
			/*font-size: .28rem;*/
			display: inline-block;
			/*color: rgb(255, 255, 255);*/
			/*line-height: .24rem;*/
			/*text-align: center;*/
			/*background-color: yellow;*/
		}
		.orderDetails-bottom .star li {
			text-align: center;
			border-radius: .5rem;
			margin: 0 auto;
			float: left;
			margin-left: .02rem;
			/*color: rgb(255,255,255);*/
			width: .3rem;
			height: .5rem;
		}
		.definite {
			border-top: .01rem solid #ccc;
			border-bottom: .01rem solid #ccc;
			height: .70rem;
			padding-top: .40rem;
			padding-bottom: .40rem;
			font-size: .25rem;
		}
		.definite .calendar {
			margin-top: .10rem;
			width: .55rem;
			height: .55rem;
			background-size: cover;
			background-image: url(../../images/calendar.png);
		}
		.definite span.mui-icon-arrowright {
			line-height: .70rem;
			font-size: .70rem;
			color: #ccc;
		}
		.definite p {
			margin-left: 10%;
			width: 40%;
		}
		.definite .hint {
			font-size: .24rem;
			color: rgb(51, 51, 51);
			line-height: .40rem;
		}
		.definite .scheme {
			display: block;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			font-size: .20rem;
			/*color: rgb(51,51,51);*/
			line-height: .40rem;
		}
		.money-specification {
			border-bottom: .01rem solid #ccc;
			padding-top: .2rem;
			padding-bottom: .2rem;
		}
		.money-specification span {
			display: block;
			height: .42rem;
			line-height: .42rem;
			text-indent: .20rem;
		}
		.money-specification h5,
		.grogshopMessage h5,
		.productExplain h5 {
			display: block;
			line-height: .50rem;
			color: rgb(51, 51, 51);
		}
		.money-specification h6,
		.productExplain h6 {
			display: block;
			line-height: .60rem;
			font-size: .22rem;
			color: rgb(242, 93, 86);
		}
		.grogshopMessage {
			line-height: .30rem;
			border-top: .01rem solid #ccc;
			border-bottom: .01rem solid #ccc;
			/*height: 150rem;*/
			padding-top: .20rem;
			padding-bottom: .20rem;
			.gain {
				margin-left: .1rem;
				background: url("../../images/star-2.png");
				background-size: cover;
				width: .26rem;
				height: .26rem;
				/*font-size: .28rem;*/
				display: inline-block;
			}
			p {
				height: .4rem;
				overflow: auto;
				line-height: .4rem;
				font-size: .25rem;
			}
			span {
				line-height: .4rem;
				font-size: .25rem;
			}
		}
		.grogshopMessage h6 {
			display: block;
			line-height: .60rem;
			font-size: .20rem;
			color: rgb(51, 51, 51);
		}
		.grogshopMessage .hint {
			font-size: .22rem;
			color: rgb(243, 93, 86);
		}
		.grogshopMessage .picture {
			width: 100%;
			height: 4.00rem;
			background-size: cover;
			background-image: url(../../images/picture.png);
		}
		.productExplain {
			border-top: .01rem solid #ccc;
			padding-top: .20rem;
			padding-bottom: .20rem;
		}
		.productExplain span {
			display: block;
			height: .3rem;
			text-indent: .20rem;
		}
		.commodity-bottom {
			height: .5rem;
		}
		.commodity-bottom .zx {
			display: inline-block;
			width: 35%;
			height: .9rem;
			font-size: .24rem;
		}
		.commodity-bottom .zx {
			text-align: center;
			line-height: .9rem;
		}
		.commodity-bottom .zx p {
			margin-left: 20%;
			width: 60%;
			height: .9rem;
		}
		.commodity-bottom .zx .mui-icon-chatboxes {
			top: 0px;
			width: .45rem;
			font-size: .45rem;
			line-height: .9rem;
			float: left;
		}
		.w {
			width: 6.9rem;
			/*text-align: center;*/
			margin: 0 auto;
		}
		.commodity-bottom .ljyd {
			height: .9rem;
			display: inline-block;
			width: 65%;
			font-size: .24rem;
			line-height: .9rem;
			background-color: rgb(255, 204, 29);
		}
	}
</style>


