<template>
	<div class="orderMessage">
		<mt-header title="订单支付" class="orderMessage-header">
			<a slot="left">
				<mt-button icon="back" @click="routerback()">返回</mt-button>
			</a>
			<!--<mt-button icon="more" slot="right"></mt-button>-->
		</mt-header>
		<div class="ticketMessage clearfix">
			<div class="flight w clearfix">
				<h5>机票信息</h5>
				<div class="flightStart">
					<p class="flightNum">
						<b class="circle">去程</b><span class="flightNumber">{{ticketinfo.goairnum}}</span>
						<span class="date">{{ticketinfo.godate.slice(0,4)}}年{{ticketinfo.godate.slice(4,6)}}月{{ticketinfo.godate.slice(6,8)}}日</span>
					</p>
					<div class="timeSite">
						<p class="fl"><span class="time">{{ticketinfo.backtime.slice(0,5)}}</span><span class="site">{{ticketinfo.startairport}}</span></p>
						<p class="fr"><span class="time">{{ticketinfo.backtime.slice(6,11)}}</span><span class="site">{{ticketinfo.destinationairport}}</span></p>
					</div>
				</div>
				<div class="flightStart">
					<p class="flightNum">
						<b class="circle">返程</b><span class="flightNumber">{{ticketinfo.backdate}}</span>
						<span class="date">{{ticketinfo.backdate.slice(0,4)}}年{{ticketinfo.backdate.slice(4,6)}}月{{ticketinfo.backdate.slice(6,8)}}日</span>
						<div class="timeSite">
							<p class="fl"><span class="time">{{ticketinfo.gotime.slice(0,5)}}</span><span class="site">{{ticketinfo.backstartairport}}</span></p>
							<p class="fr"><span class="time">{{ticketinfo.gotime.slice(6,11)}}</span><span class="site">{{ticketinfo.backdestinationairport}}</span></p>
						</div>
				</div>
			</div>

		</div>
		<div class="grogshopMessage">
			<div class="w">
				<h5>酒店信息</h5>
				<div class="hotelMsage clearfix">
					<h6 class="fl">三亚京海国际假日酒店</h6>

					<p class="fr star">星级：
						<b class="mui-icon mui-icon-star gain" v-for="item in parseInt(hotelinfo.hotellevel)"></b>
					</p>
				</div>

				<p class='coordinate'>
					<span>地址：</span><span>{{hotelinfo.hotelname}}</span>
				</p>
				<p class="phone">
					<span>电话：</span><span>{{hotelinfo.hoteltel}}</span>
				</p>
				<div class="dataTime clearfix">
					<p class="rzsj fl">入住时间：{{hotelinfo.hotelindate.slice(0,4)}}年{{hotelinfo.hotelindate.slice(4,6)}}月{{hotelinfo.hotelindate.slice(6,8)}}日</p>
					<p class="ldsj fr">离店时间：{{hotelinfo.hoteloutdate.slice(0,4)}}年{{hotelinfo.hoteloutdate.slice(4,6)}}月{{hotelinfo.hoteloutdate.slice(6,8)}}日</p>
				</div>
				<p class="price">
					<span>总价：</span><span class="jgnumber">￥{{price}}</span>
				</p>
			</div>

		</div>
		<div class='waybill'>
			<div class="crew w clearfix">
				<h5 class="fl">乘机人/使用人信息</h5>
				<div @click="addCustomer()">
					<mt-palette-button content="+" class="fr">
						<div class="my-icon-button indexicon icon-popup"></div>
						<div class="my-icon-button"></div>
						<div class="my-icon-button"></div>
					</mt-palette-button>
				</div>
				<div class="fl w passengerBox" v-for="(item, key) in customerinfo">
					<div class="fl passengerType">成人</div>
					<div class="fl inputBox">
						<mt-field label="姓名" placeholder="Input contname" v-model="item.cumname"></mt-field>
						<mt-field label="省份证" placeholder="Input  idnumber " v-model="item.idnumber"></mt-field>
					</div>
				</div>
			</div>
			<div class="crewForm w">
				<div v-for="(item, key) in contactsinfo">
					<h6>联系人</h6>
					<mt-field label="联系人姓名" v-model="item.contname"></mt-field>
					<mt-field label="联系电话" type="tel" v-model="item.conttel"></mt-field>
				</div>

			</div>

		</div>
		<nav class="mui-bar mui-bar-tab commodity-bottom" @click="orderCreate()">
			<a>
				<mt-button class="ljyd fr">
					提交
				</mt-button>
			</a>

		</nav>
	</div>
</template>

<script>
	import api from '../../api/index.js'
	import Vue from 'vue'

	export default {
		data() {
			return {
				username: 'moern',
				price: '', //产品价格
				ticketinfo: {
					"goairnum": "长安航9H7813",
					"godate": "20170810",
					"gotime": "06:30-10:10",
					"backairnum": "南航ZH4754",
					"backdate": "20170813",
					"backtime": "22:30-00:10",
					"startairport": "西安—咸阳机场T2",
					"destinationairport": "广州—白云机场T2",
					"backstartairport": "西安—咸阳机场T2",
					"backdestinationairport": "广州—白云机场T2"
				},
				hotelinfo: {
					hoteladdress: "西安啊啊啊啊",
					hotelindate: "20170810",
					hotellevel: "5",
					hotelname: "香格里拉",
					hoteloutdate: "20170813",
					hoteltel: "123456789"
				},
				customerinfo: [{
					'cumname': '',
					'idnumber': ''
				}],
				//联系人信息
				contactsinfo: [{
					'contname': '',
					'conttel': ''
				}]
			}
		},
		components: {

		},
		created() {
			this.getInitData(window.sessionStorage.proInfo, window.sessionStorage.thisDate);
		},
		methods: {
			//获取航班信息
			getInitData: function(id, time) {
				console.log(id);
				api.getnewProduct({
					proid: id,
					date: time
				}).then(response => {
					this.ticketinfo = response.proList[0].ticketinfo[0];
					this.hotelinfo = response.proList[0].hotelinfo[0];
					this.price = response.proList[0].price;
					console.log(response.data);
				})

			},
			//添加乘客信息
			addCustomer: function() {

				if(this.customerinfo.length > 4) {
					alert("最多只能同时添加五人");
					return;
				}
				this.customerinfo.push({
					'cumname': '',
					'idnumber': ''
				});
				console.log(this.customerinfo);
				
			},
			//创建订单
			orderCreate: function() {
				
//				this.onBridgeReady();
//				this.success(true);
				//				console.log(Vue);				
				//				Vue.http.options.emulateJSON = true;
				api.orderCreate({
					'proid': window.sessionStorage.proInfo,
					'date': window.sessionStorage.thisDate,
//					'customerinfo': {'customerinfo':[{},{}]},
//					'customerinfo': this.customerinfo,
					'customerinfo': JSON.stringify(this.customerinfo),
//					'contactsinfo': this.contactsinfo				
					'contactsinfo': JSON.stringify(this.contactsinfo)

				}).then(response => {
					console.log(response.data[0].odernum)
					window.sessionStorage.ordernum=response.data[0].odernum;
					this.$router.push("/CreatorOrder");
				})
			},

			//微信支付测试
			onBridgeReady: function() {
				
			},
			routerback: function() {
				this.$router.push("/CommodityDetails/" + window.sessionStorage.proInfo);
			}
		},

	}
</script>

<style lang='scss'>
	.orderMessage {
		background-color: rgb(245, 245, 249);
		.orderMessage-header {
			background-color: rgb(255, 204, 29);
		}
		.ticketMessage {
			margin-bottom: .1rem;
			/*height: 4.3rem;*/
			background-color: rgb(255, 255, 255);
			.flight {
				h5 {
					line-height: 1rem;
					color: rgb(73, 73, 73);
					font-weight: 700;
				}
				.flightStart {
					overflow: hidden;
					margin-top: .2rem;
					border-bottom: 1px dashed #ccc;
					height: 1.5rem;
					.flightNum {
						font-size: .24rem;
						height: .3rem;
						line-height: .3rem;
						.circle {
							font-size: .16rem;
							text-align: center;
							float: left;
							width: .75rem;
							background-color: rgb(255, 204, 29);
							border-radius: 15px;
						}
						.flightNumber {
							float: left;
							width: 2.5rem;
							text-align: center;
						}
						.date {
							float: right;
							width: 3.4rem;
							text-align: center;
						}
					}
					.timeSite {
						height: 1.10rem;
						p {
							width: 50%;
							.site {
								color: rgb(166, 166, 166);
							}
							span {
								line-height: .4rem;
								width: 100%;
								display: block;
								text-align: center;
							}
						}
					}
				}
			}
		}
		/*清楚浮动*/
		.clearfix:after {
			content: "";
			display: block;
			height: 0;
			clear: both;
			visibility: hidden;
		}
		/*酒店信息样式*/
		.grogshopMessage {
			margin-top: .3rem;
			border-bottom: 1px solid #ccc;
			margin-bottom: .1rem;
			padding: .1rem;
			background-color: rgb(255, 255, 255);
			h5 {
				line-height: 1rem;
				color: rgb(73, 73, 73);
				font-weight: 700;
			}
			.hotelMsage {
				line-height: .3rem;
				h6 {
					width: 50%;
					/*font-size: .18rem;*/
					font-weight: 700;
					color: rgb(73, 73, 73);
				}
				.gain {
					margin-left: .05rem;
					background: url("../../images/star-2.png");
					background-size: cover;
					width: .26rem;
					height: .26rem;
					/*font-size: .28rem;*/
					display: inline-block;
				}
				.star {
					text-align: right;
					width: 50%;
				}
			}
			.coordinate,
			.phone,
			.dataTime,
			.dataTime {
				margin-top: .2rem;
				margin-bottom: .2rem;
				line-height: .3rem;
				color: rgb(51, 51, 51);
			}
			.dataTime {
				line-height: .3rem;
				p {
					width: 50%;
					/*text-align: center;*/
				}
				.ldsj {
					text-align: right;
				}
			}
			.price {
				.jgnumber {
					font-size: .3rem;
					color: rgb(229, 63, 71);
					font-weight: 900;
				}
			}
		}
		.waybill {
			background-color: rgb(255, 255, 255);
			.crew {
				.mint-palette-button {
					color: #ccc;
					width: .5rem;
					height: .5rem;
					line-height: .5rem;
					background-color: rgb(255, 255, 255) !important;
					.mint-main-button {
						background-color: rgb(255, 255, 255) !important;
						border: 1px solid #ccc;
					}
				}
				.passengerBox {
					margin-top: .1rem;
				}
				.passengerType {
					border-right: 1px solid #ccc;
					text-align: center;
					height: 1.8rem;
					line-height: 1.8rem;
					width: 19%;
				}
				.inputBox {
					/*height: 1.8rem;*/
					overflow: hidden;
					.mint-field {
						/*height: .9rem;*/
						.mint-cell-wrapper {
							background-image: none;
						}
					}
					width: 80%;
				}
			}
			h5 {
				font-weight: 800;
				line-height: .5rem;
			}
			h6 {
				font-weight: 800;
				line-height: .5rem;
				color: rgb(73, 73, 73);
			}
		}
		.commodity-bottom {
			height: .5rem;
			.ljyd {
				height: .9rem;
				display: inline-block;
				width: 100%;
				font-size: .24rem;
				line-height: .9rem;
				background-color: rgb(255, 204, 29);
			}
		}
		.w {
			width: 6.9rem;
			/*text-align: center;*/
			margin: 0 auto;
		}
	}
</style>


