import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home/Home.vue'
import Commodity from '@/pages/commodity/commodity.vue'
import CommodityDetails from '@/pages/commodity-details/commodity-details.vue'
import OrderMessage from '@/pages/orderPage/orderMessage.vue'
import CreatorOrder from '@/pages/orderPage/creatorOrder.vue'

Vue.use(Router)
export default new Router({
//mode: 'history',
  scrollBehavior: () => ({
    y: 0
  }),
  routes: [
    { path: '/home', component: Home },
    { path: '/Commodity', component: Commodity },
    { path: '/CommodityDetails/:id', name: 'CommodityDetails', component: CommodityDetails },
    { path: '/OrderMessage', name: 'OrderMessage', component: OrderMessage },
    { path: '/CreatorOrder', component: CreatorOrder },
    { path: '/', redirect: '/home' },
    { path: '*', redirect: '/home' }
  ]
})



