import { fetch } from "./fetch.js"
export default {
    /**
     * 获取产品列表信息
     */
    getProInfo(params) {
        return fetch('/FreeTour/getProInfo', params, 'get')
    },
     /**
     * 获取产品列表信息
     */
    getProPicture(params) {
        return fetch('/FreeTour/getProPicture', params, 'get')
    },
    /**
     * 4.3.4费用及退改签规则接口
     */
     getOtherInfo(params) {
        return fetch('/FreeTour/getOtherInfo', params, 'get')
    },
    /**
     * 酒店信息
     */
     getHotelInfo(params) {
        return fetch('/FreeTour/getHotelInfo', params, 'get')
    },
    /**
     * 日期和价格选定
     */
     getProprice(params) {
        return fetch('/FreeTour/getProprice', params, 'get')
    }, 
    /**
     * 订单创建接口
     */
     orderCreate(params) {
        return fetch('/FreeTour/order/create/', params, 'get')
    },
    /**
     *产品日期选中
     */
     getnewProduct(params) {
        return fetch('/FreeTour/getnewProduct', params, 'get')
    },
    /**
     * 航班信息列表
     */
     getAirlineInfo(params) {
        return fetch('/FreeTour/getAirlineInfo', params, 'get')
    },
    
    
}


