<script>
	import { mapGetters, mapActions } from 'vuex'
	import ArticleList from '../../components/ArticleList.vue'
	import Loadmore from '../../components/LoadMore.vue'
	export default {
		name: 'home',
		data() {
			return {
				site: '三亚',
				load: 'LOADMORE_ARTICLE',
				siteList: [{
						'site': '三亚',
						'isSelect': true
					},
					{
						'site': '海口',
						'isSelect': false
					},
					{
						'site': '珠海',
						'isSelect': false
					},
					{
						'site': '大连',
						'isSelect': false
					},
					{
						'site': '青海',
						'isSelect': false
					},
					{
						'site': '西藏',
						'isSelect': false
					},
					{
						'site': '北京',
						'isSelect': false
					},
					{
						'site': '上海',
						'isSelect': false
					},
				]
			}
		},
		mounted() {
			this.get()
		},
		methods: {
			...mapActions({
				get: 'GET_INDEX_ARTICLE'
			}),
			urbanSelect: function(index) {
				this.siteList.forEach(function(e) {
					e.isSelect = false;
				});
				this.siteList[index].isSelect = true;
			}
		},
		computed: {
			...mapGetters([
				'index_article'
			]),
			userInfo() {
				return JSON.parse(window.localStorage.getItem('userInfo'))
			}
		},
		components: {
			ArticleList,
			Loadmore,
		}
	}
</script>

