<template>
	<div class="creatorOrder" v-cloak>
		<mt-header title="产品信息" class="orderMessage-header">
			<router-link to="/orderMessage" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
			<!--<mt-button icon="more" slot="right"></mt-button>-->
		</mt-header>
		<section class="ticketMessage clearfix">
			<div class="flight w clearfix">
				<h5>机票信息</h5>
				<section class="flightStart">
					<p class="flightNum"><b class="circle">去程</b><span class="flightNumber">{{ticketinfo.goairnum}}</span><span class="date">2017年10月1日</span></p>
					<div class="timeSite">
						<p class="fl"><span class="time">{{ticketinfo.gotime[0]}}</span><span class="site">{{ticketinfo.startairport}}</span></p>
						<p class="fr"><span class="time">{{ticketinfo.gotime[1]}}</span><span class="site">{{ticketinfo.destinationairport}}</span></p>
					</div>
				</section>
				<section class="flightStart">
					<p class="flightNum"><b class="circle">回程</b><span class="flightNumber">{{ticketinfo.backairnum}}</span><span class="date">2017年10月1日</span></p>
					<div class="timeSite">
						<p class="fl"><span class="time">{{ticketinfo.backtime[0]}}</span><span class="site">{{ticketinfo.backstartairport}}</span></p>
						<p class="fr"><span class="time">{{ticketinfo.backtime[1]}}</span><span class="site">{{ticketinfo.backdestinationairport}}</span></p>
					</div>
				</section>
			</div>
		</section>
		<section class="grogshopMessage">
			<div class="w">
				<h5>酒店信息</h5>
				<div class="hotelMsage clearfix">
					<h6 class="fl">{{hotelinfo.hotelname}}</h6>
					<p class="fr star">星级：
						<b class="mui-icon mui-icon-star gain" v-for="i in hotelinfo.hotellevel"></b>
					</p>
				</div>
				<p class='coordinate'>
					<span>地址：</span><span>{{hotelinfo.hoteladdress}}</span>
				</p>
				<p class="phone">
					<span>电话：</span><span>{{hotelinfo.hoteltel}}</span>
				</p>
				<p class="phone">入住时间：{{hotelinfo.indate}}</p>
				<p class="phone">离店时间：{{hotelinfo.outdate}}</p>
				<p class="price">
					<span>总价：</span><span class="jgnumber">￥{{hotelinfo.hotelprice}}</span>
				</p>
			</div>
		</section>
		<section class='waybill'>
			<div class="crew w clearfix">
				<h5 class="fl">乘机人/使用人信息</h5>
				<mt-palette-button content="+" class="fr">
					<div class="my-icon-button indexicon icon-popup"></div>
					<div class="my-icon-button"></div>
					<div class="my-icon-button"></div>
				</mt-palette-button>
				<div class="fl w passengerBox" v-for="item in customerinfo">
					<div class="fl passengerType">成人</div>
					<div class="fl inputBox">
						<mt-field label="姓名" :value="item.cumname" readonly="readonly"></mt-field>
						<mt-field label="身份证" :value="item.idnumber" readonly="readonly"></mt-field>
					</div>
				</div>
			</div>
			<div class="crewForm w">
				<div>
					<h6>联系人</h6>
					<mt-field label="联系人姓名" :value="contactsinfo.contname" readonly="readonly"></mt-field>
					<mt-field label="联系电话" :value="contactsinfo.conttel" type="tel" readonly="readonly"></mt-field>
				</div>
			</div>
		</section>
		<nav class="mui-bar mui-bar-tab commodity-bottom">
			<mt-button class="zj fl">
				总价：<span>￥{{hotelinfo.price}}</span>
			</mt-button>
			<a>
				<mt-button class="ljyd fr" @click="payFn">
					去支付
				</mt-button>
			</a>
		</nav>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		name: 'creatorOrder',
		data() {
			return {
				ticketinfo: {
					goairnum: '',
					backairnum: '',
					gotime: [],
					backtime: [],
					startairport: '',
					destinationairport: '',
					backstartairport: '',
					backdestinationairport: ''
				},
				hotelinfo: {
					hotelname: '',
					hotellevel: 0,
					hoteladdress: '',
					hoteltel: '',
					hotelprice: '',
					price: '',
					indate: '',
					outdate: ''
				},
				customerinfo: [{
					cumname: '',
					idnumber: '',
				}],
				contactsinfo: {
					contname: "",
					conttel: ""
				}
			};
		},
		created() {
			var that = this;
			console.log(window.sessionStorage.ordernum)
			axios.get('http://10.70.67.68:8080/FreeTour/order/select?ordernum=' + window.sessionStorage.ordernum)
				.then(function(response) {
					console.log('response:',response);
					if(response.data.code != 0) return; //code=0正常获取到数据				 	
					var dataObj = response.data.data[0];
					console.log('dataObj:', dataObj)
					that.ticketMessageFn(dataObj.ticketinfo);
					that.hotelMsageFn(dataObj.hotelinfo, dataObj.price);
					that.customerMessageFn(dataObj.customerinfo);
					that.contactMessageFn(dataObj.contactsinfo);
				}, function(response) {
					console.log(response);
				})
				.catch(function(err) {
					alert('异常信息：' + err);
				});
		},
		methods: {
			ticketMessageFn(data) {
				var data = data[0];
				data.gotime = data.gotime.split('-'),
					data.backtime = data.backtime.split('-');
				//console.log('data:', data);
				this.ticketinfo = data;
			},
			hotelMsageFn(data, price) {
				var data = data[0];
				data.hotellevel = +data.hotellevel,
					data.price = price,
					data.indate = data.hotelindate.slice(0, 4) + '年' + data.hotelindate.slice(4, 6) + '月' + data.hotelindate.slice(6, 8) + '日',
					data.outdate = data.hoteloutdate.slice(0, 4) + '年' + data.hoteloutdate.slice(4, 6) + '月' + data.hoteloutdate.slice(6, 8) + '日';
				//console.log('data:',data);
				this.hotelinfo = data;
			},
			customerMessageFn(data) {
				console.log(data)
//				var data = data[0];
				//console.log('data:',data);
				this.customerinfo = data;
			},
			contactMessageFn(data) {
				var data = data[0];
				//console.log('data:',data);
				this.contactsinfo = data;
			},
			payFn() {
				//				alert('调用去支付接口');
				WeixinJSBridge.invoke(
					'getBrandWCPayRequest', {
						"appId": "wx2421b1c4370ec43b", //公众号名称，由商户传入     
						"timeStamp": "1395712654", //时间戳，自1970年以来的秒数     
						"nonceStr": "e61463f8efa94090b1f366cccfbbb444", //随机串     
						"package": "prepay_id=u802345jgfjsdfgsdg888",
						"signType": "MD5", //微信签名方式：     
						"paySign": "70EA570631E4BB79628FBCA90534C63FF7FADD89" //微信签名 
					},
					function(res) {
						if(res.err_msg == "get_brand_wcpay_request:ok") {} // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。 
					}
				);

				if(typeof WeixinJSBridge == "undefined") {
					if(document.addEventListener) {
						document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
					} else if(document.attachEvent) {
						document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
						document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
					}
				} else {
					onBridgeReady();
				}
			}
		}
	};
</script>

<style lang='scss' scoped>
	[v-clock] {
		display: none
	}
	
	.creatorOrder {
		background-color: rgb(245, 245, 249);
		.orderMessage-header {
			background-color: rgb(255, 204, 29);
		}
		.ticketMessage {
			margin-bottom: .1rem;
			/*height: 4.3rem;*/
			background-color: rgb(255, 255, 255);
			.flight {
				h5 {
					line-height: 1rem;
					color: rgb(73, 73, 73);
					font-weight: 700;
				}
				.flightStart {
					overflow: hidden;
					margin-top: .2rem;
					border-bottom: 1px dashed #ccc;
					height: 1.5rem;
					.flightNum {
						font-size: .24rem;
						height: .3rem;
						line-height: .3rem;
						.circle {
							font-size: .16rem;
							text-align: center;
							float: left;
							width: .75rem;
							background-color: rgb(255, 204, 29);
							border-radius: 15px;
						}
						.flightNumber {
							float: left;
							width: 2.5rem;
							text-align: center;
						}
						.date {
							float: right;
							width: 3.4rem;
							text-align: center;
						}
					}
					.timeSite {
						height: 1.10rem;
						p {
							width: 50%;
							.site {
								color: rgb(166, 166, 166);
							}
							span {
								line-height: .4rem;
								width: 100%;
								display: block;
								text-align: center;
							}
						}
					}
				}
			}
		}
		/*清楚浮动*/
		.clearfix:after {
			content: "";
			display: block;
			height: 0;
			clear: both;
			visibility: hidden;
		}
		/*酒店信息样式*/
		.grogshopMessage {
			margin-top: .3rem;
			border-bottom: 1px solid #ccc;
			margin-bottom: .1rem;
			padding: .1rem;
			background-color: rgb(255, 255, 255);
			h5 {
				line-height: 1rem;
				color: rgb(73, 73, 73);
				font-weight: 700;
			}
			.hotelMsage {
				line-height: .3rem;
				h6 {
					width: 50%;
					/*font-size: .18rem;*/
					font-weight: 700;
					color: rgb(73, 73, 73);
				}
				.star {
					text-align: right;
					width: 50%;
				}
			}
			.coordinate,
			.phone,
			.dataTime,
			.dataTime {
				margin-top: .2rem;
				margin-bottom: .2rem;
				line-height: .3rem;
				color: rgb(51, 51, 51);
			}
			.dataTime {
				line-height: .3rem;
				p {
					width: 50%;
					/*text-align: center;*/
				}
				.ldsj {
					text-align: right;
				}
			}
			.price {
				.jgnumber {
					font-size: .3rem;
					color: rgb(229, 63, 71);
					font-weight: 900;
				}
			}
		}
		.waybill {
			background-color: rgb(255, 255, 255);
			.crew {
				.mint-palette-button {
					color: #ccc;
					width: .5rem;
					height: .5rem;
					line-height: .5rem;
					background-color: rgb(255, 255, 255) !important;
					.mint-main-button {
						background-color: rgb(255, 255, 255) !important;
						border: 1px solid #ccc;
					}
				}
				.passengerBox {
					margin-top: .1rem;
				}
				.passengerType {
					border-right: 1px solid #ccc;
					text-align: center;
					height: 1.8rem;
					line-height: 1.8rem;
					width: 19%;
				}
				.inputBox {
					height: 1.8rem;
					overflow: hidden;
					.mint-field {
						height: .9rem;
						.mint-cell-wrapper {
							background-image: none;
						}
					}
					width: 80%;
				}
			}
			h5 {
				font-weight: 800;
				line-height: .5rem;
			}
			h6 {
				font-weight: 800;
				line-height: .5rem;
				color: rgb(73, 73, 73);
			}
		}
		.commodity-bottom {
			height: .9rem;
			.zj {
				height: .9rem;
				display: inline-block;
				width: 50%;
				font-size: .24rem;
				line-height: .9rem;
				/*background-color: rgb(255, 204, 29);*/
			}
			.ljyd {
				height: .9rem;
				display: inline-block;
				width: 50%;
				font-size: .24rem;
				line-height: .9rem;
				background-color: rgb(255, 204, 29);
			}
		}
		.w {
			width: 6.9rem;
			/*text-align: center;*/
			margin: 0 auto;
		}
	}
	
	.gain {
		margin-left: .05rem;
		background: url("../../images/star-2.png");
		background-size: cover;
		width: .26rem;
		height: .26rem;
		/*font-size: .28rem;*/
		display: inline-block;
		/*color: rgb(255, 255, 255);*/
		/*line-height: .24rem;*/
		/*text-align: center;*/
		/*background-color: yellow;*/
	}
</style>


