import Vue from 'vue'
import axios from 'axios'
import qs from 'qs'
import VueAxios from 'vue-axios'

import { IS_PRODUCT, baseURL, testURL } from '../config.js'
let instance = axios.create({
  baseURL: IS_PRODUCT ? baseURL : testURL,
  timeout: 5000, // 超时
  traditional: true,
//headers: {'X-Requested-With':'XMLHttpRequest'},

//Content-Type: 'application/json;charset=utf-8'
//headers: {'X-Custom-Header': 'foobar'}
//headers: {'application/json': 'XMLHttpRequest'},
});
Vue.use(VueAxios, instance)

//POST传参序列化(添加请求拦截器)
instance.interceptors.request.use((config) => {
    //在发送请求之前做某件事
    if(config.method  === 'post'){ 
        config.data = qs.stringify(config.data);
    }
    return config;
},(error) =>{
     console.log("错误的传参", 'fail');
    return Promise.reject(error);
});

//返回状态判断(添加响应拦截器)
instance.interceptors.response.use((res) =>{
    //对响应数据做些事
    if(res.data.code != "0"){
        console.log(res.data.msg);
        return Promise.reject(res);
    }
    return res;
}, (error) => {
    console.log("网络异常", 'fail');
    return Promise.reject(error);
});

export function fetch(url, params, method) {
	if(method=='get'){
		params={params};
	}
  return new Promise((resolve, reject) => {
    instance[method](url, params)
      .then(response => {
        resolve(response.data);
      }, err => {
        reject(err);
      })
      .catch((error) => {
        reject(error)
      })
  })
}



